.loadErrorMassage {
    width: fit-content;
    font-size: 30px;
    color: red;
    text-align: center;
    border: 2px solid rgba(255, 0, 0, 0.467);
    border-radius: 20px;
    margin: 0 auto;
    padding: 50px;
}
@media (max-width: 768px) {
    .productlist {
        overflow: scroll;
        margin: 0 auto;
        min-height: 100vh;
        padding: 0 0 0 50px;
    }
}
@media (min-width: 768px) {
    .productlist {
        display: grid;
        grid-template-columns: 1fr 1fr;
        max-width: 80%;
        margin: 0 auto;
    }
}

@media (min-width: 1150px) {
    .productlist {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        max-width: 80%;
        margin: 0 auto;
    }
}
