.container{
    padding: 20px;
    background-color: white;
    border-radius: 25px;
}

.form{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.input{
        width: 50vw;
        background: rgba(255, 255, 255, 0.1) ;
        padding: 10px;
        margin: 5px;
        color: black;
        font-size: 14px;
        border: 1px #96007d52 solid;
        border-radius: 10px;
        font-family: "Open Sans", sans-serif;;
    
}

.textarea{
    height: 30vh;
}

.button{
    cursor: pointer;
    border: 1px solid #cb11ab;
    padding: 14px 30px;
    border-radius: 200px;
    color: #fff;
    background: #cb11ab;
    font-family: "Open Sans", sans-serif;
    font-size: 16px;
    margin: 5px;
    transition:all;
    transition-duration: 200ms;

}
.input:focus{
    transition:all;
    transition-duration: 200ms;
    font-size: 16px;
    box-shadow: 
    0 0 7px rgba(157, 0, 255, 0.05),
    0 0 10px rgba(157, 0, 255, 0.05),
    0 0 21px rgba(132, 0, 255, 0.05),
    0 0 42px rgba(251, 0, 255, 0.01),
    0 0 82px rgba(251, 0, 255, 0.01);;
}

.button:hover{
    background: #fff;
    color: #115dd8;
}

@media screen and (max-width: 500px ) {
    .input:focus{
        transition:all;
        transition-duration: 200ms;
        font-size: 16px;
        box-shadow: none;
    }
}